import React from 'react';
import AbstractList from '../Core/AbstractList'


class UserList extends AbstractList {

	constructor( props ) {
		super( props );

		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	getEntityName() {
		return "user";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:username", type: 'text', label: ' ' }
		]
	}

	getTableHead() {
		return ["", " " , ""];
	}

	getCreatePanel() {
		return [
			{ id: "username", type: 'text', label: ' ' },
			{ id: "newPassword", type:'password', label: "" },
			{ id: "authorities", type: "multiSelect", values: (this.state.options === undefined ? []: this.state.options.roles) ,label: "" }
		];
	}

	banUser( userId ) {
		this.rest.postCustom( userId + "/disable" )
			.then( resp => this.doSearch() );
	}

	activeUser( userId ) {
		this.rest.postCustom( userId + "/enable" )
			.then( resp => this.doSearch() );
	}

	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.username}</td>
				<td>
					<i className="btn fa fa-ban" aria-hidden="true" hidden={!data.enabled} onClick={this.banUser.bind(this, data.id)}></i>
					<i className="btn fa fa-check" aria-hidden="true" hidden={data.enabled} onClick={this.activeUser.bind(this, data.id)}></i>
					<i className="btn fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i>
				</td>
			</tr>
		);
	}

}


export default UserList;


